import React, { useState } from "react"
import { NavLink } from "react-router-dom"
import NavbarTextLink from "./NavbarTextLink.jsx"

import SearchIcon from '@material-ui/icons/Search';
import HomeIcon from '@material-ui/icons/Home';
import FolderSpecialRoundedIcon from '@material-ui/icons/FolderSpecialRounded';
import MenuIcon from '@material-ui/icons/Menu';
import MenuOpenIcon from '@material-ui/icons/MenuOpen';


function Navbar() {

    const [navbarStatus, setNavbarStatus] = useState("closed")
    const openStyle = {

        width: "180px",
    }

    const closedStyle = {

        width: "60px",
    }

    function openNavBarHandler() {
        if (navbarStatus === "open") setNavbarStatus("closed"); else setNavbarStatus("open")
    }

    return (
        <div style={(navbarStatus === "open") ? openStyle : closedStyle} className="navbar">
            <NavLink to="#" style={{ backgroundColor: "#185a9d", textDecoration: 'none', height: "54px" }} onClick={openNavBarHandler}>
                {(navbarStatus === "closed") ?
                    <div style={{ overflow: "visible" }}>
                        <MenuIcon className="navbar-icon" />
                    </div>
                    :
                    <div style={{ display: "flex", alignItems: "center", backgroundColor: "#185a9d" }}>
                        <MenuOpenIcon className="navbar-icon" />
                    </div>}

            </NavLink>

            <NavbarTextLink className="navbar-icon"
                pathLink="/home"
                name="Home"
                navbarStatus={navbarStatus}
                icon={<HomeIcon className="navbar-icon" />}
            />



            <NavbarTextLink className="navbar-icon"
                pathLink="/all"
                name="Find Tracks"
                navbarStatus={navbarStatus}
                icon={<SearchIcon className="navbar-icon" />} />

            <NavbarTextLink className="navbar-icon"
                pathLink="/personal"
                name="My Tracks"
                navbarStatus={navbarStatus}
                icon={<FolderSpecialRoundedIcon className="navbar-icon" />} />
        </div>
    )

}

export default Navbar