import React from "react"

import { NavLink} from "react-router-dom"

function NavbarTextLink(props) {

    const tileStyle = {
        color: "white",
        textDecoration: "none",
        textDecorationColor: "white",
        fontSize: "0.8rem",
        padding: "12px 20px",
        backgroundColor: "transparent"
    }


    return (
        <NavLink to={props.pathLink} style={{ textDecoration: 'none' }} onMouseEnter={(evt)=> evt.currentTarget.style.backgroundColor= "#353b3d"}
        onMouseLeave={(evt)=> evt.currentTarget.style.background="transparent"}>
            <div style={{ display: "flex", justifyContent: "left", alignItems: "center", backgroundColor: "transparent" }}>
                {props.icon}
                {(props.navbarStatus === "open") ?
                    <p style={tileStyle}> {props.name} </p>
                    : undefined}
            </div>
            
        </NavLink>
    )

}
export default NavbarTextLink