import React from "react"


function Course(props) {
    return (
        <div className = "course-item">
            <p class= "course-item-title">{props.title}</p>
            <p class= "course-item-link">{props.link}</p>
            <p class= "course-item-category">{props.category}</p>
            <p class= "course-item-author">{props.author}</p>
        </div>
    )
}
export default Course