import React, { useState } from "react"
import "./Landing.css"
import LoginForm from "../forms/LoginForm"
import RegisterForm from "../forms/RegisterForm"


function Landing() {
    const [activeForm, setActiveForm] = useState("login")
    const activeColor = { backgroundImage: "linear-gradient(to right, rgb(81, 180, 180),rgb(140, 204, 212))", color: "white" }

    function switchHandler(evt) {
        const newValue = evt.target.value
        setActiveForm(newValue)
    }
    return (
        <div className="landing-container-bckg">
            <div className="landing-container-main">
                <div className="landing-container-left">
                    <h1 style={{fontSize : "3rem"}}>PathFinder</h1>
                    <br/>
                    <h2>Start your journey</h2>
                </div>
                <div className="landing-container-right">
                    <div className="landing-container-right-formswitch">
                        <button
                            className="formswitch"
                            value="login"
                            onClick={switchHandler}
                            style={(activeForm === "login")? activeColor : null} > Sign In </button>
                        <button
                            className="formswitch"
                            value="register"
                            onClick={switchHandler}
                            style={(activeForm === "register")? activeColor : null} > Register </button>
                    </div>
                    <h1 style={{ color: "teal", marginTop: "2rem", fontFamily: "Ubuntu, sans-serif"}} >
                    {(activeForm === "login") ? "Welcome Back!" : "Join Us!"}</h1>
                    {(activeForm === "login") ? <LoginForm /> : <RegisterForm />}
                </div>
            </div>
        </div>
    )
}

export default Landing