import React from "react"

const validStyle = {
    borderColor : "#30c784",
    color: "green"
}

const invalidStyle = {
    color: "red",
    padding : "5px 10px",
    animation: "fadeIn ease 0.3s"
}


function Message(props){
    return (
        <p className = "message" style = {!(props.condition==="ok")? invalidStyle : validStyle}>
        {!(props.condition==="ok")? props.errorMessageText : props.successMessageText}
        </p>
    )
}
export default Message