import React, { useState } from "react"
import {Redirect} from "react-router-dom"
import InputField from "./InputField"
import "./LoginForm.css"
import Message from "./ReactionMessage"
import Auth from "../auth/AuthFunctions"

function LoginForm() {

    const [identifierInput, setIdentifierInput] = useState("")
    const [passwordInput, setPasswordInput] = useState("")
    const [loginStatus, setLoginStatus] = useState("")

    function onIdentifierChangeHandler(evt) {
        const newEmailValue = evt.target.value
        setIdentifierInput(newEmailValue)
    }

    function onPasswordChangeHandler(evt) {
        const newPasswordValue = evt.target.value
        setPasswordInput(newPasswordValue)
    }

    function loginFormHandler(){
        if (!(identifierInput && passwordInput)) setLoginStatus("Please fill in all the fields")
        else{
            Auth.authenticate(identifierInput, passwordInput, setLoginStatus, "Invalid Credentials")
        }

    }
    return (
        <div className="form-container">
            <form onSubmit={(e) => e.preventDefault()}>
                <div className="login-form">
                    {!(loginStatus === "ok") ? <Message errorMessageText = {loginStatus} condition = {loginStatus}/> : <Redirect to="/all" />}
                    <InputField type="identifier" placeholderMessage="email or username" onChangeHandler={onIdentifierChangeHandler} value={identifierInput} required={true}/>
                    <InputField type="password" placeholderMessage="password" onChangeHandler={onPasswordChangeHandler} value={passwordInput} required={true}/>
                    <button type="submit" className="action-button" onClick={loginFormHandler}> SIGN IN</button>
                </div>
            </form>
        </div>)
}

export default LoginForm