import React from "react"

const topBarStyle = {
    width  : "100vw",
    height: "54px",
    backgroundColor : "#185a9d",
    /*backgroundImage: "linear-gradient(to bottom right, #185a9d, #3285a8)",*/
    position : "fixed",
    top : 0 ,
    left : "60px",
    right: 0,
    textAlign : "left",
    zIndex: 3,
}

function TopBar(){
    return (
        <div style = {topBarStyle}>
            <a style= {{textDecoration : "none"}}href="/home"><h1 style={{fontSize: "1rem", position: "relative", left : "10px", top : "18px", color: "white", zIndex: 3 }}> PathFinder</h1></a>
        </div>
    )
}

export default TopBar