class Auth {

    status=""
    authenticate(identifier, password, cb, onErrorMessage = undefined) {
        console.log("logging in")
        fetch('/api/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({"identifier": identifier, "password": password })
        }).then(response => {
            if (response.status === 200) return response.json();
        })
            .then(data => {
                if (data) {
                    localStorage.setItem('access_token', data['access_token'])
                    localStorage.setItem('refresh_token', data['refresh_token'])
                    localStorage.setItem('user', "testuser")
                    localStorage.setItem('isLoggedIn', true)
                    cb("ok")
                } else {
                    cb(onErrorMessage)
                }
            }
            )

    }


    isLoggedIn() {
        const refToken = localStorage.getItem('refresh_token')
        if (!refToken) {
            console.log("logged out")
            return false
        }

        // console.log(refToken)
        fetch('/api/refresh', {
            method: 'GET',
            headers: {
                'Authorization': `Bearer ${refToken}`
            },

        })
            .then(response => {
                if (response.status === 200) return response.json();
                else {
                    console.log("redirect to login")
                    this.status = false
                }
            })
            .then(data => {
                if (data) {
                    localStorage.setItem('access_token', data['access_token'])
                    console.log("isLoggedIn")
                    this.status = true
                }
                else {
                    console.log("redirect to login")
                    this.status = false
                }
            })
        return this.status
    }

    register(username, email, password, setRegisterStatus, setErrorMessage, onErrorMessage = undefined){
        console.log("logging in")
        fetch('/api/register', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ "username": username, "email": email, "password": password })
        }).then(response => {
            if (response.status === 201) return response.json();
        }).then(data => {
                if (data) {
                    setRegisterStatus("ok")
                } else {
                    setRegisterStatus("error")
                    setErrorMessage("error on register")
                }
            }
            )
    }
}

export default new Auth()
