import React, { useState } from "react"
import InputField from "./InputField"
import "./LoginForm.css"
import Message from "./ReactionMessage"
import Auth from "../auth/AuthFunctions"

function RegisterForm() {

    const [emailInput, setEmailInput] = useState("")
    const [usernameInput, setUsernameInput] = useState("")
    const [passwordInput, setPasswordInput] = useState("")
    const [passwordInput2, setPasswordInput2] = useState("")
    const [registerStatus, setRegisterStatus] = useState("")

    const [errorMessage, setErrorMessage] = useState("")
    function onEmailChangeHandler(evt) {
        const newEmailValue = evt.target.value
        setEmailInput(newEmailValue)
    }

    function onUsernameChangeHandler(evt){
        const newUsernameInput = evt.target.value
        setUsernameInput(newUsernameInput)
    }

    function onPasswordChangeHandler(evt) {
        const newPasswordValue = evt.target.value
        setPasswordInput(newPasswordValue)
    }

    function onPasswordChangeHandler2(evt){
        const newPasswordValue = evt.target.value
        setPasswordInput2(newPasswordValue)
    }

    function registerFormHandler(){
        if (!(emailInput && passwordInput && passwordInput2)) setErrorMessage("Please fill in all the fields")
        else if (!(passwordInput===passwordInput2)) {setRegisterStatus('error'); setErrorMessage("Passwords are not matching")}
        else{
                Auth.register(usernameInput, emailInput, passwordInput, setRegisterStatus, setErrorMessage)
        }

    }
    return (
        <div className="container">
            <form onSubmit={(e) => e.preventDefault()}>
                <div className="login-form">
                    {(registerStatus === "error") ? 
                    <Message errorMessageText = {errorMessage} condition = {registerStatus}/>
                    :<Message successMessageText = "Successfully created your account !" condition = {registerStatus}/> }
                    <InputField type="mail" placeholderMessage="mail" onChangeHandler={onEmailChangeHandler} value={emailInput} required={true}/>
                    <InputField type="username" placeholderMessage="username" onChangeHandler={onUsernameChangeHandler} value={usernameInput} required={true}/>
                    <InputField type="password" placeholderMessage="password" onChangeHandler={onPasswordChangeHandler} value={passwordInput} required={true}/>
                    <InputField type="password" placeholderMessage="confirm password" onChangeHandler={onPasswordChangeHandler2} value={passwordInput2} required={true}/>
                    <button type="submit" className="action-button" onClick={registerFormHandler}> REGISTER </button>
                </div>
            </form>
        </div>)
}

export default RegisterForm